import Vue from "vue";
import "./plugins/axios";
import App from "./App.vue";
import "./assets/tailwind.css";
import router from "./router";
import store from "./store";
import vueCustomElement from "vue-custom-element";
import OpenAi from "./components/OpenAi.vue"; // Ensure correct filename case

Vue.config.productionTip = false;

Vue.use(vueCustomElement); // âœ… Register before Vue instance
Vue.customElement("chat-bot", OpenAi); // âœ… Define custom element before mounting

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
